
.about-section {
    background-color: #f9f9f9; 
    border-radius: 8px; 
    padding: 20px; 
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); 
    margin-bottom: 20px; 
}

.about-title {
    font-size: 24px;
    font-weight: bold;
    color: #333333; 
    margin-bottom: 15px;
}
  
.about-paragraph {
    color: #555555; 
    margin-bottom: 15px;
}

  